@font-face {
  font-family: "Comfortaa";
  src: local("Comfortaa"),
    url("./assets/fonts/Comfortaa-Light.eot") format("eot"),
    url("./assets/fonts/Comfortaa-Light.woff2") format("woff2"),
    url("./assets/fonts/Comfortaa-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

